.wrapper {
    overflow: hidden;
    position: relative;
    width: 100%; /* real length of dropdown */
  }
  
  .wrapper:after {
    color: purple;
    border-radius: 50%;
    padding: 2px 2px;
    font-size: 0.8rem;
    pointer-events: none; /* ▼ click triggers dropdown */
    position: absolute;
    right: 0.2rem;
    top: 0.3rem;
    z-index: 1;

  }
  option { 
    /* Whatever color  you want */
    background-color: #5A46D6;
    font-size: large;
    color: white;
    height: auto;
    border-radius: 10rem;
    padding: 12px
}
option:disabled{
  color: gray;
}

  select {

    width: 300px;
    padding: 12px;
    border: 0 !important;
    /* needed */
    -webkit-appearance: none;
    -moz-appearance: none;
    appearance: none;
    /* SVG background image */
     background-image: url("data:image/png;base64,");
    background-size: 1rem;
    background-position: calc(100% - 1.3em) 9px;
    background-repeat: no-repeat;
  }
  .select-dropdown::after{
    background: #34495e;
    transition: all .4s ease-in-out;
  }
  select::-ms-expand {
      display: none;
  }

  #style-11::-webkit-scrollbar-track
{
	-webkit-box-shadow: inset 0 0 6px rgba(0,0,0,0.3);
	background-color: #F5F5F5;
	border-radius: 10px;
}

#style-11::-webkit-scrollbar
{
	width: 5px;
	background-color: #F5F5F5;
}

#style-11::-webkit-scrollbar-thumb
{
	background-color: #5A46D6;
	border-radius: 10px;
	background-image: -webkit-linear-gradient(0deg,
   rgba(255, 255, 255, 0.5) 25%,
		transparent 25%,
		transparent 50%,
		rgba(255, 255, 255, 0.5) 50%,
		rgba(255, 255, 255, 0.5) 75%,
		transparent 75%,
		transparent)
}

.swap-btn:hover {opacity: 0.8;}